
.welcome-left-aside{position:fixed;top:5em;display:inline-block;width:140px
}
@media (max-width: 600px){
.welcome-left-aside{display:none
}
}
.aside-list{color:#909090
}
.aside-list>li{margin:0 0 .3em;padding:.4em .6em;cursor:pointer;-webkit-transition:color .3s;transition:color .3s
}
.aside-list>li.true{font-weight:700
}
.aside-list>li.true,.aside-list>li:active,.aside-list>li:hover{color:#fff;background-color:rgba(222,222,222,0.2);border-radius:2px
}
.aside-list>li.true .active,.aside-list>li:active .active,.aside-list>li:hover .active{display:inline-block
}
.aside-list>li .active,.aside-list>li.true .inactive,.aside-list>li:active .inactive,.aside-list>li:hover .inactive{display:none
}
.aside-list>li .inactive{display:inline-block
}
.aside-list .icon{background-size:100% 100%;width:1.5em;height:1.5em;top:.2em
}
.aside-list .icon,.aside-list span{display:inline-block;position:relative
}
.aside-list span{top:-.2em
}
.aside-list-span{margin-left:.9em
}



